<!--
 * @Descripttion: 登记台列表
 * @Author: 曾元球
 * @Date: 2019-06-28 14:24:26
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 14:52:58
 -->
<template>
  <div class='page flex-v'
    style="position:relative">
    <div class='head flex-v-center flex-justify'>
      <div class='head-title'>登记台</div>
      <div class='head-btn'
        v-if='btns.hasRegister'
        data-btn-id='101001001'
        @click='$router.push({name: "PersonList"})'>
        <img src='@/assets/img/file.png'
          alt='登记' />&nbsp;
        <span>登记</span>
      </div>
    </div>
    <div class='query-form'>
      <el-form inline
        size='small'
        class='default-form-style'
        ref="query-form">
        <el-form-item label='登记日期：'>
          <el-date-picker value-format='yyyy-MM-dd'
            v-model='dateRange'
            type='daterange'
            range-separator='至'
            start-placeholder='开始日期'
            end-placeholder='结束日期'
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label='体检流水号：'>
          <el-input placeholder='请输入'
            v-model='queryObj.sysGuid'
            clearable></el-input>
        </el-form-item>
        <el-form-item label='姓名：'>
          <el-input placeholder='请输入'
            v-model='queryObj.userName'
            clearable></el-input>
        </el-form-item>
        <el-form-item label='身份证号：'>
          <el-input placeholder='请输入'
            v-model='queryObj.idNo'
            clearable></el-input>
        </el-form-item>
        <el-form-item label='机构名称：'>
          <el-select placeholder='全部'
            v-model='queryObj.createOrganizationId'
            clearable>
            <el-option :value='item.sysGuid'
              v-for='item in orgList'
              :key='item.sysGuid'
              :label='item.oname'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label='体检状态：'>
          <el-select placeholder='请选择'
            v-model='queryObj.examStatus'
            @clear='queryObj.examStatus="all"'
            :clearable='queryObj.examStatus!=="all"'>
            <el-option value='all'
              label='全部'></el-option>
            <el-option value='success'
              label='已完成'></el-option>
            <el-option value='notSuccess'
              label='未完成'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon='el-icon-search'
            @click='onSubmit'
            round>查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class='table-box flex-item'>
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type="index">
          <template v-slot='scope'>
            {{scope.$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='体检流水号'
          prop='sysGuid'
          min-width='100'
          show-overflow-tooltip>
          <template v-slot='scope'>
            <a class='link'
              @click='gocheckdetail(scope.row.sysGuid)'>{{scope.row.sysGuid}}</a>
          </template>
        </el-table-column>
        <el-table-column label='姓名'
          prop='userName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='性别'
          width='60'>
          <template v-slot='scope'>
            {{
              scope.row.sexType == 1 ? '男' :
              scope.row.sexType == 2 ? '女' : '不详'
            }}
          </template>
        </el-table-column>
        <el-table-column label='年龄'
          prop='age'
          width='60'></el-table-column>
        <el-table-column label='民族'>
          <template v-slot='scope'>{{scope.row.nationCase | emptyData}}</template>
        </el-table-column>
        <el-table-column label='身份证号'
          prop='idNo'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='机构名称'
          show-overflow-tooltip>
          <template v-slot='scope'>{{scope.row.createOrganizationName | emptyData}}</template>
        </el-table-column>
        <el-table-column label='联系电话'
          width="120">
          <template v-slot='scope'>{{scope.row.contactPhone | emptyData}}</template>
        </el-table-column>
        <el-table-column label='体检日期'
          width='100'>
          <template v-slot='scope'>{{scope.row.examDate | formatDate}}</template>
        </el-table-column>
        <el-table-column label='责任医生'
          prop='createUserName'></el-table-column>
        <el-table-column label='数据来源'
          prop='dataSource'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='体检状态'
          width='100'>
          <template v-slot='scope'>
            <div :style='{color: scope.row.examStatus==5 ? "red" : "#07CBA7"}'>
              {{scope.row.examStatus == 5 ? '已完成' : '未完成'}}
            </div>
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template v-slot='scope'>
            <i class='icon iconfont bj-medical-report'
              v-if='scope.row.examStatus==5'
              @click='report(scope.row.sysGuid)'></i>
            <template v-else>
              <i class='icon iconfont bj-edit'
                @click='edit(scope.row.sysGuid)'
                v-if='btns.hasUpdate&&scope.row.examStatus==1'
                data-btn-id='101001002'></i>
              <i class='icon iconfont bj-delete'
                @click='delConfirm(scope.row)'
                v-if='btns.hasDel&&scope.row.examStatus==1'
                data-btn-id='101001003'></i>
              <i class='iconfont bj-search'
                v-if="scope.row.examStatus>1"
                @click="gocheckdetail(scope.row.sysGuid)"></i>
            </template>
          </template>
        </el-table-column>

      </el-table>
    </div>
    <div class='foot'>
      <bj-pagination :query-obj='queryObj'
        query-url='/user/examUser/listPaging'
        @listResult='getListResult'
        ref='paginationEvent'
        @controlLoading='controlLoading' />
    </div>

    <el-dialog :visible.sync='showPreview'
      class="flex"
      title="体检报告"
      width="1120px"
      center>
      <preview-report :examUserId='examUserId'
        ref="report" />
      <div slot="footer">
        <el-button class='btn-hollowing'
          @click="showPreview=false">取消</el-button>
        <el-button class='btn-info'
          @click="$refs.report.printPage()">打印</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import PreviewReport from '../total-check/previewReport'
import { formatDate } from '@/lib/utils'
import { mapState } from 'vuex'
export default {
  name: 'CheckIn',
  components: { PreviewReport },
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    let nowDate = formatDate(new Date())
    return {
      loading: false,
      queryObj: {
        startCreateDate: nowDate,
        endCreateDate: `${nowDate} 23:59:59`,
        sysGuid: '',
        userName: '',
        idNo: '',
        residenceAddress: '',
        createOrganizationId,
        examStatus: 'all'
      },
      pageNum: 1,
      pageSize: 10,
      dateRange: [nowDate, `${nowDate} 23:59:59`],
      tableData: [],
      examUserId: '',
      showPreview: false
    }
  },

  mounted() {
    this.$refs['query-form'].$el.addEventListener('keyup', e => {
      console.log(e.keyCode)
    })
  },

  methods: {
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    controlLoading(loading) {
      this.loading = loading
    },

    // 查询
    onSubmit() {
      this.$refs.paginationEvent.initPageList(1)
    },

    //详情
    gocheckdetail(tjsysGuid) {
      this.$router.push({
        name: 'CheckInDetail',
        query: { tjsysGuid, sysGuid: '' }
      })
    },

    //编辑
    edit(tjsysGuid) {
      this.$router.push({
        name: 'CheckInPerson',
        query: { tjsysGuid, sysGuid: '' }
      })
    },

    //报告
    report(examUserId) {
      this.examUserId = examUserId
      this.showPreview = true
    },

    //删除
    delConfirm({ sysGuid, examStatus }) {
      if (examStatus > 1) return
      this.$confirm('请确定记录没有已检项目', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          let url = '/user/examUser/remove'
          this.$post(url, { sysGuid, examStatus })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length === 1) {
                let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
                this.$refs.paginationEvent.initPageList(pageNum)
              } else {
                this.$refs.paginationEvent.initPageList()
              }
            })
            .catch(error => {
              this.$alert(`原因：${error}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(err => err)
    }
  },
  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasRegister: ~btnIds.indexOf('101001001'), // 登记按钮权限
        hasUpdate: ~btnIds.indexOf('101001002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('101001003') // 删除按钮权限
      }
    },

    ...mapState(['orgList'])
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.queryObj.startCreateDate = val[0]
        this.queryObj.endCreateDate = `${val[1]} 23:59:59`
      } else {
        this.queryObj.startCreateDate = ''
        this.queryObj.endCreateDate = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.redcolor {
  color: red;
}
.lvcolor {
  color: #07cba7;
}
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}

.icon {
  color: @color-green;
  cursor: pointer;
}

.foot {
  height: 60px;
}
</style>


